@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap);
.cart-qty-container {
    border: 2px solid #1a121257;
    width: 70px;
    height: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.cart-qty__modify {
    font-size: 1.2rem;
    cursor: pointer;
}
.cart-item-container {
  width: 80%;
  margin: 0 auto;
  border-bottom: 1px solid #1a121257;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cart-item__delete {
  color: #ee0f0f;
  cursor: pointer;
}

.cart-item__description > p {
  margin-top: 0;
}

.cart-item__description {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.cart-item__info {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.cart-item__info p {
  margin-top: 3rem;
}

.cart-item-container img {
  width: 90px;
  height: 90px;
  object-fit: cover;
}

@media (max-width: 450px) {
  .cart-item-container {
    width: 95%;
  }
}

.cart-list-container {
  width: 100%;
}

.cart-container {
  position: fixed;
  z-index: 99;
  right: 0;
  top: 0;
  background-color: white;
  width: 40%;
  height: 100%;
}

.cart-container__header {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 2rem;
}

.cart-container hr {
  width: 95%;
  margin-bottom: 1rem;
}

@media (max-width: 768px) {
  .cart-container {
    width: 100%;
  }
}

.cart-details-container {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  padding: 1rem;
}

.cart-details-container__total-price,
.cart-details-container__shipping-price {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.free-shipping-indicator-container {
  text-align: center;
  margin-bottom: 2rem;
}

.free-shipping-indicator__main {
  height: 8px;
  background-color: #ccc;
  width: 80%;
  margin: 0 auto;
}

.free-shipping-indicator__main__counter {
  height: 8px;
  background-color: #1a1212;
  transition: all 0.5s ease-in-out;
}

.header-container {
  width: 100%;
  position: fixed;
  top: 0;
  background-color: transparent;
  transition: all 0.3s ease-in-out;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
}

.header-container__motto p {
  margin: 0;
  padding: 0;
  font-size: 0.7rem;
}

.header-container a {
  text-decoration: none;
  color: black;
}

.header-container__cart {
  cursor: pointer;
}

.homepage-conatiner__hero {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  text-align: center;
  margin-top: 8rem;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.hero-item {
  width: 40%;
  line-height: 1.2;
  font-size: 1.3rem;
}

.hero-item img {
  width: 100%;
  height: 450px;
  margin-bottom: 1rem;
  object-fit: cover;
}

.homepage-container__showcase h1 {
  margin-top: 3rem;
  text-align: center;
}

.homepage-container__showcase-slider {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 90%;
  max-width: 1440px;
  margin: 0 auto;
}

.slider-item {
  width: 30%;
  margin: 0.4rem 1rem;
}

.homepage-container__showcase-slider img {
  height: 300px;
  width: 350px;
  object-fit: contain;
}



@media (max-width: 768px) {
  .hero-item {
    width: 80%;
    margin: 1rem auto;
    font-size: 1rem;
  }

  .hero-item img {
    height: 400px;
  }

  .homepage-container__showcase-slider {
    overflow-x: scroll;
    justify-content: flex-start;
  }

.homepage-container__showcase-slider img {
  width: 200px;
  height: 200px;
}

  .slider-item {
    width: 80%;
  }
}

.product-images-container {
  width: 80%;
  margin-right: 1rem;
}

.product-images-container img,
.product-images-container video {
  width: 300px;
  height: 300px;
  object-fit: cover;
  margin: 0.5rem;
  background-color: #f2f1f1;
}

@media (max-width: 1024px) {
  .product-images-container img,
  .product-images-container video {
    width: 200px;
    height: 220px;
  }
}

@media (max-width: 768px) {
  .product-images-container img,
  .product-images-container video {
    width: 300px;
    height: 320px;
  }

  .product-images-container {
    display: flex;
    flex-wrap: nowrap;
    overflow-y: hidden;
    width: 100%;
    padding: 5px;
    overflow-x: auto;
  }
}

.product-description-container h1 {
    margin-bottom: 0;
}

.product-description-container p {
    margin-top: 0.5rem;
    line-height: 1.5;
    font-size: 1.03rem;
}

.product-description-container hr {
    margin: 1rem 0;
}

.size-list-container {
  display: flex;
  flex-wrap: wrap;
  margin: 0.5rem 0;
}

.size-item-container {
  border: 2px solid #1a1212;
  background-color: transparent;
  padding: 0.8rem;
  margin: 0 0.5rem 0.5rem 0;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.size-item-container:hover:not(.size-active) {
  background-color: #1a121235;
}

.size-active {
  background-color: #1a1212;
  color: white;
}

.add-to-cart-btn {
  border: none;
  margin-top: 1rem;
  color: white;
  padding: 1rem;
  width: 100%;
  transition: all 0.3s ease-in-out;
}

.add-to-cart-btn-active {
  background-color: #1a1212;
  cursor: pointer;
}

.add-to-cart-btn-disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.add-to-cart-btn-active:hover {
  background-color: transparent;
  color: #1a1212;
  border: 1px solid #1a1212;
}

.review-item-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 90%;
  margin: 0 auto;
  border-bottom: 1px solid #1a1212;
}

.review-item__main {
  width: 50%;
}

@media (max-width: 767px) {
  .review-item-container {
    flex-direction: column;
    width: 95%;
    align-items: flex-start;
  }
  
  .review-item__main {
    order: 3;
  }

  .review-item__main {
    width: 100%;
  }
}

.star-rating-container > .las,
.star-rating-container > .lar, 
.star-rating-container > .la-star {
  font-size: 16px;
}

.review-list-container {
  margin: 0 1rem;
  padding: 0;
}

.product-page-container {
  padding: 40px;
  margin: 0 0.25rem;
}

.project-page-container__info {
  display: flex;
  margin: 5rem auto;
  padding: 0.5rem;
  max-width: 1440px;
}

@media (max-width: 768px) {
  .product-page-container {
    padding: 0;
  }

  .project-page-container__info {
    display: block;
  }
}

.product-item-container {
  padding: 1rem;
  margin: 1rem;
  width: 300px;
  height: 300px;
  background-color: #fff;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.1);
}

.product-item-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product-item-container__imgBg {
  background-color: #fcf8f3;
  width: 95%;
  height: 80%;
  margin: 0 auto;
}

.product-item-container__info {
  display: flex;
  justify-content: space-between;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #1a1212;
}
.products-list {
    display: flex;
    flex-wrap: wrap;
    width: 90%;
    justify-content: center;
    max-width: 1440px;
    margin: 3rem auto;
}

.product-list-container h1 {
    text-align: center;
    margin: 8rem 0 0;
}

.footer-container {
  margin-top: 5rem;
  width: 100%;
  background-color: white;
  color: #1a1212;
}
.footer-container main {
  max-width: 1440px;
  margin: 0 auto;
}
.footer-container input {
  width: 300px;
  border: none;
  border-bottom: 2px solid #dcdcdc;
  padding: 15px;
  background-color: #1a12125e;
  transition: all 0.3s ease-in-out;
  margin-bottom: 1em;
  color: white;
}

.footer-container input:focus {
  outline: none;
}
.footer-container .form {
  padding: 30px 55px 0;
}
.footer-container .form p {
  margin-bottom: 5px;
}
.footer-container .footer-grid {
  display: flex;
  justify-content: space-around;
  max-width: 40rem;
}
.footer-container .footer-links {
  padding: 25px;
}
.footer-container ul {
  padding: 0;
  margin: 0;
}
.footer-container h4 {
  margin-bottom: 0;
}
.footer-container li {
  list-style: none;
  padding: 10px 0;
  font-size: 13px;
  cursor: pointer;
}

@media (max-width: 767px) {
  .footer-container .footer-grid {
    display: block;
    margin: 0 auto;
    text-align: center;
  }
  .footer-container .footer-links {
    padding: 10px;
  }
  .footer-container .form {
    padding: 30px 30px 0;
  }
  .footer-containerinput {
    width: 280px;
  }
}

/*  Jost  */
* {
  box-sizing: border-box;
  font-family: 'Josefin Sans', Arial, Helvetica, sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

body,
html {
  padding: 0;
  margin: 0;
  background-color: #fcf8f3;
  color: #1a1212;
}

hr {
  height: 2px;
  background-color: #1a1212;
  border: none;
}

/* ============== custom classes */

.btn {
  padding: 1rem;
  letter-spacing: 2px;
  text-transform: uppercase;
  background-color: #241c15;
  color: #fff;
  border: 1px solid #241c15;
  transition: background-color 0.3s ease-in-out;
  cursor: pointer;
  outline: none;
}

li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.btn:hover {
  background-color: transparent;
  color: #241c15;
}

